.section {
  height: 690px;
  background: #fff;
  background-size: cover;
  clip-path: polygon(0% 0%, 100% 5%, 100% 100%, 0% 95%);
  transform: translate(0, -10%);
  padding-top: 110px;
  background: no-repeat url('../../img/background/fourthScreenSomethingMob.png');
  background-position: 100% 10%;
}

@media(min-width: 1200px) {
  .section {
    height: 100vh;
    clip-path: polygon(0% 0%, 100% 10%, 100% 100%, 0% 90%);
    transform: translate(0, -20vh);
    padding-top: 10%;
    position: relative;
    background: no-repeat url('../../img/background/fourthScreenSomethingDesk.png');
    background-position: 100% 10%;
  }
  .navigation {
    position: absolute;
    left: 60px;
    top: 30vh;
  }
  .img {
    display: block;
    position: absolute;
    height: auto;
    width: 35vw;
    left: 55%;
    bottom: -5vh;
  }
}
